import React, {Component} from 'react';
import data from '../../data/data';

import myPhoto from '../../img/myPhoto.JPG';

class AboutMe extends Component {
  constructor() {
    super();

    this.state = {
      
    };
  };

  render() {
    return(
      <section className='aboutMe main__aboutMe'>
        <article className='aboutMe__content'>
          <img src={myPhoto} alt="I'm scary and I know it" className='aboutMe__photo'/>
          <div className='aboutMe__textBlock'>
            <h2 className='aboutMe__heading'>{data.aboutMeHeadingCurrent.toUpperCase()}</h2>
            <span className='aboutMe__name'>{data.aboutMeNameCurrent}</span>
            <p className='aboutMe__text'>
              {data.aboutMeTextCurrent1}<br/><br/>
              {data.aboutMeTextCurrent2}<br/><br/>
              {data.aboutMeTextCurrent3}<br/><br/>
            </p>
          </div>
        </article>
      </section>
    );
  };
};

export default AboutMe;